.blogs .box_container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
    gap:1.5rem;
}

.blogs .box_container .box{
    border:var(--border);
}

.blogs .box_container .box .image{
    height: 25rem;
    overflow:hidden;
    width: 100%;
}

.blogs .box_container .box .image img{
    height: 100%;
    object-fit: cover;
    width: 100%;
}

.blogs .box_container .box:hover .image img{
    transform: scale(1.2);
}

.blogs .box_container .box .content{
    padding:2rem;
}

.blogs .box_container .box .content .title{
    font-size: 2.5rem;
    line-height: 1.5;
    color:#fff;
}

.blogs .box_container .box .content .title:hover{
    color:var(--main-color);
}

.blogs .box_container .box .content span{
    color:var(--main-color);
    display: block;
    padding-top: 1rem;
    font-size: 2rem;
}

.blogs .box_container .box .content p{
    font-size: 1.6rem;
    line-height: 1.8;
    color:#ccc;
    padding:1rem 0;
}

.heading {
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    padding-bottom: 3.5rem;
    font-size: 4rem;
}

.heading span {
    color: var(--main-color);
    text-transform: uppercase;
}

.btn {
    margin-top: 1rem;
    display: inline-block;
    padding: 0.9rem 3rem;
    font-size: 1.7rem;
    color: #fff;
    background: var(--main-color);
    cursor: pointer;
}

.btn:hover{
    letter-spacing: .2rem;
}