.navbar a {
    margin: 0 1rem;
    font-size: 1.6rem;
    color: #fff;
}

.navbar a:hover {
    color: var(--main-color);
    border-bottom: .1rem solid var(--main-color);
    padding-bottom: .5rem;
}

.icons div {
    display: inline-block;
    color: #fff;
    cursor: pointer;
    font-size: 2.5rem;
    margin-left: 2rem;
}

.icons div:hover {
    color: var(--main-color);
}

.icons .fas_fa_bars {
    display: none;
}

.search_form {
    position: absolute;
    top: 115%;
    right: 7%;
    background: #fff;
    width: 50rem;
    height: 5rem;
    display: flex;
    align-items: center;
    transform: scaleY(0);
    transform-origin: top;
}

.search_form.active {
    transform: scaleY(1);
}

.search_form input {
    height: 100%;
    width: 100%;
    font-size: 1.6rem;
    color: var(--black);
    padding: 1rem;
    text-transform: none;
}

.search_form label {
    cursor: pointer;
    font-size: 2.2rem;
    margin-right: 1.5rem;
    color: var(--black);
}

.search_form label:hover {
    color: var(--main-color);
}

.cart_items_container {
    position: absolute;
    top: 100%;
    right: -100%;
    height: calc(100vh - 9.5rem);
    width: 35rem;
    background: #fff;
    padding: 0 1.5rem;
}

.cart_items_container.active {
    right: 0;
}

.cart_items_container .cart_item {
    position: relative;
    margin: 2rem 0;
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.cart_items_container .cart_item .fa_times {
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
    cursor: pointer;
    color: var(--black);
}

.cart_items_container .cart_item .fa_times:hover {
    color: var(--main-color);
}

.cart_items_container .cart_item img {
    height: 7rem;
}

.cart_items_container .cart_item .content h3 {
    font-size: 2rem;
    color: var(--black);
    padding-bottom: .5rem;
}

.cart_items_container .cart_item .content .price {
    font-size: 1.5rem;
    color: var(--main-color);
}

.cart_items_container .btn {
    width: 100%;
    text-align: center;
}

@media (max-width: 991px) {

    html {
        font-size: 55%;
    }

    .header {
        padding: 1.5rem 2rem;
    }

    section {
        padding: 2rem;
    }

}

@media (max-width: 768px) {

    .icons .fas_fa_bars {
        display: inline-block;
    }

    .navbar {
        position: absolute;
        top: 100%;
        right: -100%;
        background: #fff;
        width: 30rem;
        height: calc(100vh - 9.5rem);
    }

    .navbar.active {
        right: 0;
    }

    .navbar a {
        color: var(--black);
        display: block;
        margin: 1.5rem;
        padding: .5rem;
        font-size: 2rem;
    }

    .search_form {
        width: 90%;
        right: 2rem;
    }
}

@media (max-width: 450px) {

    html {
        font-size: 50%;
    }
}